.root {
	position: relative;
	width: 450px;
	height: 520px;
	-webkit-transform: scale(0.8);
	        transform: scale(0.8);
}

.root.small {
	-webkit-transform: scale(0.5);
	        transform: scale(0.5);
}

.head {
	position: absolute;
	left: 10px;
	bottom: 200px;
	z-index: 3;
}

.body {
	position: absolute;
	bottom: 0px;
	left: 90px;
	z-index: 2;
}

.tail {
	position: absolute;
	bottom: 55px;
	left: 275px;
	z-index: 1;
	-webkit-transform-origin: -150px 250px;
	        transform-origin: -150px 250px;
}



