:root {
	--primary-color: #2c48eb;
	--active-color: #97a4f3;
}

.root {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	font-weight: bold;
	width: 100%;
}

.characterHolder {
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.button {
	padding: 5px;
	margin: 10px 20px;
	border: 0;
	border-bottom: 4px solid #2c48eb;
	border-bottom: 4px solid var(--primary-color);
	background: transparent;
	color: #2c48eb;
	color: var(--primary-color);
	font-weight: bold;
	font-size: 24px;
	-webkit-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.button:hover {
	border-color: #97a4f3;
	border-color: var(--active-color);
	color: #97a4f3;
	color: var(--active-color);
}

.header {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	margin-bottom: 1rem;
}

.slider {
	width: 90%;
}

.sliderThumb {
	border-radius: 50%;
}

.slider .sliderFill {
	background: #97a4f3;
	background: var(--active-color);
}

.sliderTrack {
	border-radius: 4px;
}

.slider label {
	padding: 15px 0;
	display: block;
}

@font-face {
	font-family: 'icomoon';
	src:  url('../assets/icomoon.woff?kbtvv7') format('woff');
	font-weight: normal;
	font-style: normal;
  }

.iconSound:before {
	content: "\e900";
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	-webkit-font-feature-settings: normal;
	        font-feature-settings: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}



